<template>
  <div class="register-page">
    <van-nav-bar title="面经注册" />
    <van-form @submit="onSubmit">
      <van-field
        label="用户名"
        placeholder="用户名"
        name="username"
        v-model="username"
        :rules="[
          { required: true, message: '请输入用户名' },
          { pattern: /^\w{6,16}$/, message: '用户名必须为6-16位' },
        ]"
      ></van-field>
      <van-field
        label="密码"
        type="password"
        placeholder="密码"
        name="password"
        v-model="password"
        :rules="[
          { required: true, message: '请输入密码' },
          { pattern: /^\w{6,16}$/, message: '用户名必须为6-16位' },
        ]"
      ></van-field>
      <div style="margin: 16px">
        <van-button type="primary" block>提交</van-button>
      </div>
    </van-form>
    <router-link to="/login" class="link">有账号，去登录</router-link>
  </div>
</template>

<script>
import { register } from '@/api/user'

export default {
  name: 'register-page',
  data() {
    return {
      username: '',
      password: '',
    }
  },
  methods: {
    async onSubmit(value) {
      await register(value)
      this.$toast.success('注册成功')
      this.$router.push('/login')
    },
  },
}
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
